<template>
  <section class="box-typical box-typical-dashboard panel panel-default scrollable">
    <header class="box-typical-header panel-heading">
        <h3 class="panel-title">{{ title }}</h3>
    </header>
    <div class="box-typical-body panel-body">
        <table class="tbl-typical">
            <tr>
                <th v-for="header in headers"><div>{{ header }}</div></th>
            </tr>
            <tr v-for="row in content">
                <td v-for="col in row">
                    <span v-if="Array.isArray(col)">
                        <span v-for="item in col" class="label" v-bind:class="{'label-primary': item == 'paid',
                                                                               'label-success': item == 'open' || item == 'active',
                                                                               'label-warning': item == 'progress',
                                                                               'label-danger': item == 'closed'}">
                            {{ item }}
                        </span>
                    </span>
                    <span v-else>{{ col }}</span>
                </td>
            </tr>
        </table>
    </div><!--.box-typical-body-->
</section><!--.box-typical-dashboard-->
</template>

<style scoped type="text/css">
    .label {
        margin: 2px;
    }
</style>

<script>
export default {
  name: 'table-box',
  props: ['title', 'headers', 'content'],
  data () {
    return { }
  }
}
</script>


