<template>
  <div>
    <div class="row">
      <div class="col-sm-12 col-lg-9">
        <chart-box></chart-box>
      </div>
      <div class="col-lg-3">
        <div>
          <statistic-box color="purple" number="12" caption="Closes tickets" percent="11"></statistic-box>
        </div>
        <div>
          <statistic-box color="green" number="29" caption="Here is an example of a long name" percent="84"></statistic-box>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-xl-6 dahsboard-column">
        <table-box title="Recent orders" :headers="['Status', 'Clients', 'Orders#', 'Date']" :content="this.$store.state.recentOrders"></table-box>
      </div>
      <div class="col-xl-6 dahsboard-column">
        <table-box title="Recent tickets" :headers="['Status', 'Subject', 'Department', 'Date']" :content="this.$store.state.recentTickets"></table-box>
      </div>
      <div class="col-xl-12 dahsboard-column">
        <table-box title="User segments" :headers="['Status', 'Subject', 'Department', 'Date']" :content="this.$store.state.recentOrders"></table-box>
      </div>
    </div>
  </div>
</template>

<script>
import chartBox from './ChartBox'
import statisticBox from './StatisticBox'
import tableBox from './TableBox'

export default {
  name: 'main',
  components: {
    chartBox,
    statisticBox,
    tableBox
  }
}
</script>


