<template>
  <article class="statistic-box" v-bind:class="color">
      <div>
          <div class="number">{{ number }}</div>
          <div class="caption"><div>{{ caption }}</div></div>
          <div class="percent">
              <div class="arrow down"></div>
              <p>{{ percent }}%</p>
          </div>
      </div>
  </article>
</template>

<style scoped type="text/css">
</style>

<script>
export default {
  name: 'statistic-box',
  props: ['color', 'number', 'caption', 'percent'],
  data () {
    return { }
  }
}
</script>


